<template>
    <div id="charts" class="content">
        <div id="main"  :style="{width:'1200px',height:'600px'}"></div>
    </div>
</template>
<script>
import echarts from 'echarts'
export default {
  data () {
    return {
      msg: '123'
    }
  },
  mounted () {
    var myChart = echarts.init(document.getElementById('main'))
    var option = {
      title: {
        text: '某楼盘买家来源',
        subtext: '饼图',
        x: 'center'
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c} ({d}%)'
      },
      legend: {
        orient: 'vertical',
        left: 'left',
        data: ['本县居民', '城乡居民', '本省其它市居民', '北上广居民', '其它外地人口']
      },
      series: [{
        name: '买家来源',
        type: 'pie',
        radius: '55%',
        center: ['50%', '60%'],
        data: [{
          value: 335,
          name: '本县居民'
        }, {
          value: 310,
          name: '城乡居民'
        }, {
          value: 234,
          name: '本省其它市居民'
        }, {
          value: 135,
          name: '北上广居民'
        }, {
          value: 1548,
          name: '其它外地人口'
        }],
        itemStyle: {
          emphasis: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      }]
    }
    myChart.setOption(option)
  }
}
</script>
<style scoped lang="less">
    
</style>



