<template>
    <div id="charts" class="content">
        <div id="main"  :style="{width:'1200px',height:'600px'}"></div>
    </div>
</template>
<script>
  import echarts from 'echarts'
  export default {
    data () {
      return {
        msg: '123'
      }
    },
    mounted () {
      var myChart = echarts.init(document.getElementById('main'))
      var option = {
        title: {
          text: '某楼盘销售',
          subtext: '实时动态数据'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#283b56'
            }
          }
        },
        legend: {
          data: ['最新成交价', '预购队列']
        },
        toolbox: {
          show: true,
          feature: {
            dataView: {
              readOnly: false
            },
            restore: {},
            saveAsImage: {}
          }
        },
        dataZoom: {
          show: false,
          start: 0,
          end: 100
        },
        xAxis: [{
          type: 'category',
          boundaryGap: true,
          data: (function () {
            var now = new Date()
            var res = []
            var len = 10
            while (len--) {
              res.unshift(now.toLocaleTimeString().replace(/^\D*/, ''))
              now = new Date(now - 2000)
            }
            return res
          })()
        }, {
          type: 'category',
          boundaryGap: true,
          data: (function () {
            var res = []
            var len = 10
            while (len--) {
              res.push(len + 1)
            }
            return res
          })()
        }],
        yAxis: [{
          type: 'value',
          scale: true,
          name: '价格/万',
          max: 30,
          min: 0,
          boundaryGap: [0.2, 0.2]
        }, {
          type: 'value',
          scale: true,
          name: '预购量',
          max: 1200,
          min: 0,
          boundaryGap: [0.2, 0.2]
        }],
        series: [{
          name: '预购队列',
          type: 'bar',
          xAxisIndex: 1,
          yAxisIndex: 1,
          data: (function () {
            var res = []
            var len = 10
            while (len--) {
              res.push(Math.round(Math.random() * 1000))
            }
            return res
          })()
        }, {
          name: '最新成交价',
          type: 'line',
          data: (function () {
            var res = []
            var len = 0
            while (len < 10) {
              res.push((Math.random() * 10 + 5).toFixed(1) - 0)
              len++
            }
            return res
          })()
        }]
      }
      var app = {}
      app.count = 11
      setInterval(function () {
        var axisData = (new Date()).toLocaleTimeString().replace(/^\D*/, '')

        var data0 = option.series[0].data
        var data1 = option.series[1].data
        data0.shift()
        data0.push(Math.round(Math.random() * 1000))
        data1.shift()
        data1.push((Math.random() * 10 + 5).toFixed(1) - 0)

        option.xAxis[0].data.shift()
        option.xAxis[0].data.push(axisData)
        option.xAxis[1].data.shift()
        option.xAxis[1].data.push(app.count++)

        myChart.setOption(option)
      }, 2100)
    }
  }
</script>
<style scoped lang="less">
    
</style>



