<template>
  <div class="layout" :class="{'layout-hide-text': spanLeft < 3}">
    <Row type="flex">
        <i-col :span="spanLeft" class="layout-menu-left">
            <Menu active-name="1" theme="dark" width="auto" accordion>
                <div class="layout-logo-left"></div>
                <Submenu name="统计">
                    <template slot="title">
                        <Icon type="ios-navigate"></Icon>
                        统计
                    </template>
                    <Menu-item name="实时统计"><router-link to="/">实时概况</router-link></Menu-item>
                    <Menu-item name="来源分布"><router-link to="/circle">来源分布</router-link></Menu-item>
                    <Menu-item name="商圈建设"><router-link to="/build">商圈建设</router-link></Menu-item>
                </Submenu>
                <Submenu name="用户管理">
                    <template slot="title">
                        <Icon type="ios-keypad"></Icon>
                        用户管理
                    </template>
                    <Menu-item name="修改密码"><router-link to="/password">修改密码</router-link></Menu-item>
                    <Menu-item name="查看用户"><router-link to="/userlist">查看用户</router-link></Menu-item>
                    <Menu-item name="新增用户"><router-link to="/form">新增用户</router-link></Menu-item>
                </Submenu>
            </Menu>
        </i-col>
        <i-col :span="spanRight">
            <div class="layout-header">
                <i-button type="text" @click="toggleClick">
                    <Icon type="navicon" size="32"></Icon>
                </i-button>
            </div>
            <div class="layout-breadcrumb">
                <Breadcrumb>
                    <Breadcrumb-item href="#">统计</Breadcrumb-item>
                    <Breadcrumb-item>实时概况</Breadcrumb-item>
                </Breadcrumb>
            </div>
            <div class="layout-content">
                <div class="layout-content-main">
                    <router-view></router-view>
                </div>
            </div>
            <div class="layout-copy">
                2017 &copy; WuLiSenSen
            </div>
        </i-col>
    </Row>
  </div>
</template>
<script>
  export default {
    name: 'app',
    data () {
      return {
        spanLeft: 4,
        spanRight: 20
      }
    },
    computed: {
      iconSize () {
        return this.spanLeft === 5 ? 14 : 24
      }
    },
    methods: {
      toggleClick () {
        if (this.spanLeft === 4) {
          this.spanLeft = 2
          this.spanRight = 22
        } else {
          this.spanLeft = 4
          this.spanRight = 20
        }
      }
    }
  }
</script>
<style lang="less">
    @import './styles/common.css';
    .layout{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }
    .layout-breadcrumb{
        padding: 10px 15px 0;
    }
    .layout-content{
        min-height: 800px;
        margin: 15px;
        overflow: hidden;
        background: #fff;
        border-radius: 4px;
    }
    .layout-content-main{
        padding: 10px;
    }
    .layout-copy{
        text-align: center;
        padding: 10px 0 20px;
        color: #9ea7b4;
    }
    .layout-menu-left{
        background: #464c5b;
    }
    .layout-header{
        height: 60px;
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
    .layout-logo-left{
        width: 90%;
        height: 30px;
        background: #5b6270;
        border-radius: 3px;
        margin: 15px auto;
    }
    .layout-ceiling-main a{
        color: #9ba7b5;
    }
    .layout-hide-text .layout-text{
        display: none;
    }
    .ivu-col{
        transition: width .2s ease-in-out;
    }
    .ivu-menu-vertical li.ivu-menu-item {
        padding: 0;
        height: 49px;
        line-height: 49px;
        a {
            width: 100%;
            height: 100%;
            display: block;
        }
    }
</style>



