import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Circle from '@/components/circle'
import Form from '@/components/f'
import Password from '@/components/password'
import Userlist from '@/components/userlist'
import Build from '@/components/build'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Index',
    component: Index
  }, {
    path: '/circle',
    name: 'Circle',
    component: Circle
  }, {
    path: '/form',
    name: 'Form',
    component: Form
  }, {
    path: '/password',
    name: 'Password',
    component: Password
  }, {
    path: '/userlist',
    name: 'Userlist',
    component: Userlist
  }, {
    path: '/build',
    name: 'Build',
    component: Build
  }]
})



